import * as api from "../lib/web3js/index";
import { show, hide } from "react-functional-modal";
import ChainInteraction from "../components/ChainInteraction/ChainInteraction";
import {
  EVENT_MSG_SUBMITTING,
  EVENT_MSG_SUBMITTTED,
  EVENT_MSG_SUBMITT_FAILED,
  EVENT_TRANS_BEGIN,
  EVENT_TRANS_END,
  MODAL_OPTIONS,
} from "../utils/Constants";
import emitter from "../utils/Emitter";
import { buried } from "../utils/utils";
import executor from "./Executor";

class ApiProxy {
  status = "waiting";
  async request(method, params = [], options = {}) {
    let res = null;
    const { subject, onAccept, onReject, onWarning, onFinish, approveTip, title, content } = options
    if (options.write) {
      params.push({
        deriBased : options.deriBased,
        onAccept: (hash) => {
          this.show(subject, "success", approveTip);
          onAccept && onAccept();
          window.setTimeout(() => {
            this.close(subject);
            if (options.isExecutor) {
              emitter.emit(EVENT_MSG_SUBMITTING, { title: title, content: content, hash: hash });
            } else {
              emitter.emit(EVENT_TRANS_BEGIN, { title: title, content: content, hash: hash });
            }
            onFinish && onFinish();
          }, 2000);
        },
        onReceipt: () => {
          window.setTimeout(() => this.close(subject), 2000);
        },
        onReject: () => {
          this.show(subject, "reject", approveTip);
          window.setTimeout(() => this.close(subject), 2000);
          onReject && onReject();
        },
        onWarning: () => {
          this.show(subject, "warning", approveTip);
          onWarning && onWarning();
        },
      });
      this.show(subject, "pending", approveTip);
    }
    if(options.isExecutor){
      executor.subscribe(params[2], params[0], params[1], title, content);
    }
    res = await api[method].call(this, ...params);
    res = this.processResponse(res, options);

    if (options.isExecutor) {
      if (options.write && options.includeResponse && res) {
      console.log("EVENT_MSG_SUBMITT_FAILED,EVENT_MSG_SUBMITTTED")
        const hash = res.success ? res.response.data.transactionHash : res.response.transactionHash;
        const error = !res.success && res.response.error;
        emitter.emit(!res.success ? EVENT_MSG_SUBMITT_FAILED : EVENT_MSG_SUBMITTTED, {
          title: title,
          content: content,
          context: { success: res.success, hash: hash, error: error },
        });
      }
    } else {
      this.emitEvent(options, res, onFinish, title, content);
    }
    return res;
  }

  emitEvent(options, res, onFinish, title, content) {
    if (options.write && options.includeResponse && res) {
      const hash = res.success ? res.response.data.transactionHash : res.response.transactionHash;
      const error = !res.success && res.response.error;
      emitter.emit(EVENT_TRANS_END, {
        title: title,
        content: content,
        context: { success: res.success, hash: hash, error: error },
      });
    }
  }

  syncRequest(method, params = [], options = {}) {
    const res = api[method].call(this, ...params);
    return this.processResponse(res, options);
  }

  close(subject) {
    hide(this.getMessageKey(subject));
  }

  getMessageKey(subject) {
    return `transaction-box-${subject.split(/\s+/).join("-")}`;
  }

  show(subject, status, approveTip) {
    const key = this.getMessageKey(subject);
    this.close(key);
    const options = {
      ...MODAL_OPTIONS,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 1001,
      },
      key: key,
    };
    show(
      <ChainInteraction
        title={subject}
        status={status}
        options={{ approveTip }}
        close={() => this.close(subject)}
      />,
      options
    );
  }

  processResponse(res, options) {
    if (options.includeResponse) {
      if (res && res.response) {
        return res;
      }
    } else {
      if (res && res.response) {
        return res.response.data;
      }
    }
    return res;
  }
}

export default new ApiProxy();
