import { useEffect, useState, useRef } from 'react';
import Icon from "../Icon/Icon"
import { inject, observer } from 'mobx-react'
import Emitter from "../../utils/Emitter";
import { EVENT_MSG_FILLING, EVENT_MSG_SUBMITTING, EVENT_MSG_FILLED, EVENT_MSG_FILL_FAILED, EVENT_MSG_SUBMITTTED, EVENT_MSG_SUBMITT_FAILED, EVENT_TRANS_BEGIN, EVENT_TRANS_END } from '../../utils/Constants'
import './transactionState.less'
import Loading from './Loading'
import useLang from '../../hooks/useLang';
import classNames from 'classnames';
function TransactionState({ wallet, intl }) {
  const lang = useLang(intl.dict, 'trade-state');
  const [isShow, setIsShow] = useState(false)
  const [tradeEnd, setTradeEnd] = useState(false)
  const [isTrade, setIsTrade] = useState(false)
  const [fillingTag, setFillingTag] = useState(false)
  const [filledTag, setFilledTag] = useState(false)
  const [messageInfo, setMessageInfo] = useState({})
  const [fillingInfo, setFillingInfo] = useState({})
  const [filledInfo, setFilledInfo] = useState({})
  const [tradeMessageInfo, setTradeMessageInfo] = useState({})
  const endTag = useRef()
  const closeBox = () => {
    setIsShow(false);
    clear()
  }
  const clear = () => {
    setTradeEnd(false);
    setFillingTag(false)
    setFilledTag(false)
    setIsTrade(false)
    setMessageInfo({});
    setFillingInfo({})
    setTradeMessageInfo({})
    setFilledInfo({})
    endTag.current = false
  }
  const onMessageInfoBgein = (data) => {
    let info = {}
    setIsTrade(false)
    if (endTag.current) {
      endTag.current = false
      return;
    }
    clear()
    info["icon"] = data.title.icon
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.hash}`
    info["hash"] = data.hash
    info["title"] = data.title.text
    info["content"] = data.content.success
    info["type"] = "processing"
    info["state"] = lang["pending"]
    setMessageInfo(info)
    setIsShow(true)
  }
  const onMessageInfoEnd = (res) => {
    let info = {}
    if (!res.context) {
      closeBox()
      return false;
    }
    const hash = res.context.hash
    info["icon"] = res.title.icon
    endTag.current = true
    if (res.context.success) {
      info["type"] = "success"
      info["state"] = lang["executed"]
      info["content"] = res.content.success
      info["title"] = res.title.text
      info["hash"] = hash
      info["link"] = `${wallet.blockExploreUrl}/tx/${hash}`
      setMessageInfo(info)
    } else {
      if (hash === "") {
        closeBox()
        info["type"] = "error"
        info["state"] = lang["failed"]
        info["title"] = res.title.text
        info["error"] = `${res.content.error} : ${res.context.error}`
        info["content"] = `${res.content.error} : ${res.context.error}`
        setMessageInfo(info)
        setIsShow(true)
        return false;
      }
      info["type"] = "error"
      info["title"] = res.title.text
      info["content"] = `${res.content.error} : ${res.context.error}`
      info["hash"] = hash
      info["link"] = `${wallet.blockExploreUrl}/tx/${hash}`
      setMessageInfo(info)
    }
    setIsShow(true)
  }
  const onTradeMessageSubmitting = (data) => {
    console.log('submitting')
    let info = {}
    console.log(endTag.current)
    if (endTag.current) {
      endTag.current = false
      return;
    }
    clear()
    info["type"] = "processing"
    info["state"] = lang["submitting"]
    info["content"] = data.content.success
    info["icon"] = data.title.icon
    info["title"] = data.title.text
    info["hash"] = data.hash
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.hash}`
    setIsTrade(true)
    setTradeMessageInfo(info)
    setIsShow(true)
  }
  const onTradeMessageSubmitted = (data) => {
    console.log('submitted')
    let info = {}
    endTag.current = true
    console.log(endTag.current)
    info["type"] = "success"
    info["state"] = lang["submitted"]
    info["content"] = data.content.success
    info["title"] = data.title.text
    info["icon"] = data.title.icon
    info["hash"] = data.context.hash
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.context.hash}`
    setTradeMessageInfo(info)
    setIsTrade(true)
    setIsShow(true)

  }
  const onTradeMessageSubmitFailed = (data) => {
    console.log('submitted error')
    let info = {}
    info["type"] = "error"
    info["state"] = lang["submitted"]
    info["content"] = `${data.content.error} : ${data.context.error}`
    info["icon"] = data.title.icon
    info["title"] = data.title.text
    info["hash"] = data.context.hash
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.context.hash}`
    setTradeMessageInfo(info)
    setIsTrade(true)
    setIsShow(true)
  }
  const onTradeMessageFilling = (data) => {
    console.log('filling', data)
    let info = {}
    info["type"] = "processing"
    info["state"] = lang["filling"]
    info["content"] = data.content.success
    info["icon"] = data.title.icon
    info["title"] = data.title.text
    info["hash"] = data.context.hash
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.context.hash}`
    setFillingInfo(info)
    setIsTrade(true)
  }
  const onTradeMessageFilled = (data) => {
    console.log('filled', data)
    let info = {}
    info["type"] = "success"
    info["state"] = lang["filled"]
    info["content"] = data.content.success
    info["icon"] = data.title.icon
    info["title"] = data.title.text
    info["hash"] = data.context.hash
    info["price"] = data.context.price
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.context.hash}`
    setFilledInfo(info)
    setIsTrade(true)
  }
  const onTradeMessageFillFailed = (data) => {
    console.log('filled error')
    let info = {}
    info["type"] = "error"
    info["state"] = lang["filled"]
    info["content"] = `${data.content.error} : ${data.context.error}`
    info["title"] = data.title.text
    info["icon"] = data.title.icon
    info["hash"] = data.context.hash
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.context.hash}`
    setFilledInfo(info)
    setIsTrade(true)
  }
  useEffect(() => {
    Emitter.on(EVENT_TRANS_BEGIN, onMessageInfoBgein)
    Emitter.on(EVENT_TRANS_END, onMessageInfoEnd)
    Emitter.on(EVENT_MSG_SUBMITTING, onTradeMessageSubmitting)
    Emitter.on(EVENT_MSG_SUBMITTTED, onTradeMessageSubmitted)
    Emitter.on(EVENT_MSG_SUBMITT_FAILED, onTradeMessageSubmitFailed)
    Emitter.on(EVENT_MSG_FILLING, onTradeMessageFilling)
    Emitter.on(EVENT_MSG_FILLED, onTradeMessageFilled)
    Emitter.on(EVENT_MSG_FILL_FAILED, onTradeMessageFillFailed)
    return () => {
      Emitter.off(EVENT_TRANS_BEGIN, onMessageInfoBgein)
      Emitter.off(EVENT_TRANS_END, onMessageInfoEnd)
      Emitter.off(EVENT_MSG_SUBMITTING, onTradeMessageSubmitting)
      Emitter.off(EVENT_MSG_SUBMITTTED, onTradeMessageSubmitted)
      Emitter.off(EVENT_MSG_SUBMITT_FAILED, onTradeMessageSubmitFailed)
      Emitter.off(EVENT_MSG_FILLING, onTradeMessageFilling)
      Emitter.off(EVENT_MSG_FILLED, onTradeMessageFilled)
      Emitter.off(EVENT_MSG_FILL_FAILED, onTradeMessageFillFailed)
    }
  }, [])
  useEffect(() => {
    let timer;
    let timerEndTag;
    if (messageInfo.type === "success") {
      timer = window.setTimeout(() => {
        closeBox();
      }, 8000)
      timerEndTag = window.setTimeout(() => {
        endTag.current = false
      }, 2000)
    } else if (messageInfo.type === "error") {
      timer = window.setTimeout(() => {
        endTag.current = false
      }, 2000)
    }
    return () => {
      timer && clearTimeout(timer);
      timerEndTag && clearTimeout(timerEndTag);
    }
  }, [messageInfo])

  useEffect(() => {
    if (tradeEnd) {
      let timer = window.setTimeout(() => {
        closeBox();
      }, 8000)
      return () => {
        timer && clearTimeout(timer)
      }
    }
  }, [tradeEnd])

  useEffect(() => {
    if (tradeMessageInfo.type === "error") {
      setIsTrade(false)
      endTag.current = false
    }
  }, [tradeMessageInfo.type])

  useEffect(() => {
    let timer
    if (tradeMessageInfo.state === lang["submitted"] && tradeMessageInfo.type !== "error") {
      timer = window.setTimeout(() => {
        setFillingTag(true)
        console.log("filling strat")
        let info = JSON.parse(JSON.stringify(tradeMessageInfo))
        info["type"] = "processing"
        info["state"] = lang["filling"]
        info["hash"] = ""
        info["link"] = ""
        setIsShow(true)
        setTradeMessageInfo(info)
      }, 1000)
    }
    return () => {
      timer && clearTimeout(timer)
    }
  }, [tradeMessageInfo.state, tradeMessageInfo.type])

  useEffect(() => {
    if (tradeMessageInfo.state === lang["filling"]) {
      console.log("filledTag true")
      setFilledTag(true)
    }
  }, [tradeMessageInfo.state, tradeMessageInfo])

  useEffect(() => {
    if (filledTag && filledInfo.hash) {
      console.log("filledInfo tradeMessageInfo")
      let info = JSON.parse(JSON.stringify(filledInfo))
      setIsShow(true)
      setTradeEnd(true)
      setTradeMessageInfo(info)
    }
  }, [filledTag, filledInfo])


  useEffect(() => {
    if (fillingTag && fillingInfo.hash) {
      console.log("filling hash")
      setTradeMessageInfo(fillingInfo)
    }
  }, [fillingTag, fillingInfo])

  useEffect(() => {
    let timer
    if (!tradeMessageInfo.hash && tradeMessageInfo.state === lang["filling"]) {
      timer = window.setTimeout(() => {
        if (!tradeMessageInfo.hash && tradeMessageInfo.state === lang["filling"]) {
          console.log("filled error timeout")
          let info = JSON.parse(JSON.stringify(tradeMessageInfo))
          info["type"] = "error"
          info["state"] = lang["filled"]
          info["content"] = "Network Connection Lost: Please refresh the page to check your position status"
          info["hash"] = ""
          info["link"] = ""
          setTradeMessageInfo(info)
        }
      }, 10000)
    }
    return () => {
      timer && clearTimeout(timer)
    }
  }, [tradeMessageInfo])

  return (
    <>
      {isShow ? <div className='transaction-state'>
        <div className="close" onClick={closeBox}>
          <div className='close-icon'>
            <Icon token="close-modal-icon" width='6' height="6" />
          </div>
        </div>
        <div className='message-title'>
          <div className='icon message-title-text'>
            <Icon token={messageInfo.icon || tradeMessageInfo.icon} width="20" height="20" />
            {messageInfo.title ? messageInfo.title : tradeMessageInfo.title}
          </div>

          {(messageInfo.hash || tradeMessageInfo.hash) ? <a rel='noreferrer' target='_blank' href={messageInfo.link || tradeMessageInfo.link}>
            <div className='state-box'>
              <span>{messageInfo.state || tradeMessageInfo.state}</span>
              {(messageInfo.type === 'processing' || tradeMessageInfo.type === 'processing') && <Loading borderColor='#5577FD' bgColor='rgba(85, 119, 253, 0.1)' />}
              {(messageInfo.type === 'success' || tradeMessageInfo.type === 'success') && <Icon token="success" width="18" height="18" />}
              {(messageInfo.type === 'error' || tradeMessageInfo.type === 'error') && <Icon token="error" width="18" height="18" />}
            </div>
          </a> : <div className='state-box'>
            <span>{messageInfo.state || tradeMessageInfo.state}</span>
            {(messageInfo.type === 'processing' || tradeMessageInfo.type === 'processing') && <Loading borderColor='#5577FD' bgColor='rgba(85, 119, 253, 0.1)' />}
            {(messageInfo.type === 'success' || tradeMessageInfo.type === 'success') && <Icon token="success" width="18" height="18" />}
            {(messageInfo.type === 'error' || tradeMessageInfo.type === 'error') && <Icon token="error" width="18" height="18" />}
          </div>}
          <div className='title-slider-box'>
            <div className={classNames('title-slider-value', {
              "trade-error-value": messageInfo.type === "error" || tradeMessageInfo.type === 'error',
              "trade-half-value": messageInfo.type === "processing" || (tradeMessageInfo.type !== "error" && tradeMessageInfo.state === lang["submitted"]),
              "trade-end-value": messageInfo.type === "success" || (tradeMessageInfo.state === lang["filled"] && tradeMessageInfo.type === "success"),
              "trade-ending-value": tradeMessageInfo.type === "processing" && tradeMessageInfo.state === lang["filling"],
              "trade-start-value": tradeMessageInfo.type === "processing" && tradeMessageInfo.state === lang["submitting"]
            })}></div>
          </div>
        </div>
        <div className='message-text-link'>
          {(!isTrade || messageInfo.type === "error" || tradeMessageInfo.type === "error") && <div className='message-text-box'>
            <div className={messageInfo.type === 'success' || tradeMessageInfo.type === "success" ? 'link' : messageInfo.type === "error" || tradeMessageInfo.type === "error" ? 'link error' : "link processing"}>
              <div className={tradeMessageInfo.type ==="error" && !tradeMessageInfo.hash &&  tradeMessageInfo["state"] === lang["filled"] ?'message-no-hash socket-error':'message-no-hash'}>
                {messageInfo.content || tradeMessageInfo.content}
              </div>
            </div>
          </div>}
          {isTrade && tradeMessageInfo.type !== "error" && <div className='trade-info-box'>
            <div className='info-box'>
              <div className='info-box-title'>{lang["price"]}</div>
              <div className='info-box-value'>
                {tradeMessageInfo.price ?  tradeMessageInfo.price : <div className='loading'></div>}
              </div>
            </div>
            <div className='info-box'>
              <div className='info-box-title'>{lang["size"]}</div>
              <div className='info-box-value'>{tradeMessageInfo.content}</div>
            </div>
          </div>}
        </div>
      </div> : null
      }
    </>

  )
}
export default inject('wallet', "intl")(observer(TransactionState))