import WebSocket from "./WebSocket";
import {
  EVENT_EXECUTOR_CANCELD,
  EVENT_EXECUTOR_MINED,
  EVENT_EXECUTOR_REQUESTED,
  EVENT_MSG_FILLED,
  EVENT_MSG_FILLING,
  EVENT_MSG_FILL_FAILED,
  EXECUTOR_SUBSCRIBE_EVENT,
  EXECUTOR_UNSUBSCRIBE_EVENT,
} from "../utils/Constants";
import { getTradeEventLogs } from "../lib/web3js";
import emitter from "../utils/Emitter";
import { bg } from "../utils/utils";

const ws = new WebSocket(process.env.REACT_APP_EXECUTOR_WSS_URL);

class Executor {
  account = null;

  subscribe(account, chainId, poolAddress, title, content) {
    this.poolAddress = poolAddress;
    this.chainId = chainId;
    this.title = title;
    this.content = content;
    if (account !== this.account) {
      ws.subscribe(
        EXECUTOR_SUBSCRIBE_EVENT,
        { account },
        (data) => {
          this.stateProcess(data);
        },
        EXECUTOR_SUBSCRIBE_EVENT,
        false
      );
    }
    this.account = account;
  }

  async stateProcess(data) {
    const { status, execute_txhash, cancel_txhash, error_string } = data;
    if (status === EVENT_EXECUTOR_REQUESTED && execute_txhash) {
      emitter.emit(EVENT_MSG_FILLING, {
        title: this.title,
        content: this.content,
        context: { success: true, hash: execute_txhash },
      });
    } else if (/^close/.test(status)) {
      const segments = status.split(":");
      if (segments[1].trim() === EVENT_EXECUTOR_MINED) {
        try {
          const { tradeCost, tradeVolume } = await this.getTradePrice(
            this.chainId,
            this.poolAddress,
            data.execute_txhash
          );
          emitter.emit(EVENT_MSG_FILLED, {
            title: this.title,
            content: this.content,
            context: {
              success: true,
              hash: execute_txhash,
              price: tradeCost ? bg(tradeCost).div(tradeVolume).toFixed(3) : "--",
            },
          });
        } catch(e) {
          console.error('error stateProcess with status ',status,e)
          emitter.emit(EVENT_MSG_FILLED, {
            title: this.title,
            content: this.content,
            context: {
              success: true,
              hash: execute_txhash,
              price: "--",
            },
          });
        }
      } else if (segments[1].trim() === EVENT_EXECUTOR_CANCELD) {
        emitter.emit(EVENT_MSG_FILL_FAILED, {
          title: this.title,
          content: this.content,
          context: { success: false, hash: cancel_txhash, error: error_string },
        });
      }
    }
  }

  unsubscribe(account) {
    if (this.account === account) {
      ws.unsubscribe(EXECUTOR_UNSUBSCRIBE_EVENT, { account }, () => {
        this.account = null;
      });
    }
  }

  async getTradePrice(chainId, poolAddress, tx) {
    return await getTradeEventLogs(chainId, poolAddress, tx);
  }
}

export default new Executor();
