  getReceipt(tx) {
    const options = { n: 30, minWait: 0, maxWait: 100 }
    return retry(
      () =>
        this.web3.eth.getTransactionReceipt(tx).then((receipt) => {
          if (receipt === null) {
            console.debug(`Retrying tranasaction receipt for ${tx}`);
            throw new RetryableError();
          }
          return receipt;
        }),
      options
    )
  }

  async parseEventLogs(tx, symbolManagerAddress) {
    const {promise} = this.getReceipt(tx);
    try {
      const receipt = await promise;
      if (!receipt) {
        // throw new Error('can not find trade event')
        return {
          tradeCost: 0,
          tradeVolume: 0,
        };
      }
      const logs = receipt.logs
      const event = this.contractAbi.find(i => i.type === 'event' && i.name === 'Trade')
      if (!event) {
        throw new Error('can not find trade event  in abi')
      }

      const parsedLogs = logs.filter(log => log.address === symbolManagerAddress && log.data !== '0x' && log.data !== '0X')
        .map(log => {
          return this.web3.eth.abi.decodeLog(
            event.inputs,
            log.data,
            log.topics
          )
        })
      if (parsedLogs.length > 0) {
        const data = event.inputs.reduce((acc, input) => {
          let value = parsedLogs[0][input.name]
          if (input.type === 'int256') {
            value = fromWei(value)
          }
          acc[input.name] = value
          return acc;
        }, {})
        return data;
      } else {
        throw new Error('can not find logs when decode log in event')
      }
    } catch (error) {
      console.debug("retry times oversize");
      return {
        tradeCost: 0,
        tradeVolume: 0,
      };
    }

  }

